"use client";

import { founders, medicoTeam, techTeam, designTeam, TeamMember } from "@/data/team";

interface TeamSectionProps {
  team: TeamMember[];
  title: string;
}

export default function FullTeamSection({ team, title }: TeamSectionProps) {
  return (
    <section className="py-24 px-6 bg-gradient-to-br from-sky-50 via-blue-50 to-teal-50">
      <h2 className="text-4xl md:text-5xl font-bold text-center text-teal-800 mb-16 drop-shadow-md">
        {title}
      </h2>

      <div className="max-w-5xl mx-auto flex flex-col gap-12">
        {team.map((member, idx) => (
          <div
            key={idx}
            className="flex flex-col md:flex-row items-center md:items-start gap-6 bg-white rounded-2xl shadow-md p-6 hover:shadow-xl transition"
          >
            {/* Image */}
            <div className="flex-shrink-0 w-36 h-36 md:w-40 md:h-40 rounded-full overflow-hidden bg-gradient-to-br from-sky-100 to-indigo-100">
              <img
                src={member.image}
                alt={member.name}
                className="object-cover w-full h-full"
              />
            </div>

            {/* Name + Description */}
            <div className="text-center md:text-left flex-1">
              <h3 className="text-2xl md:text-3xl font-bold text-gray-900 mb-3">
                {member.name}
              </h3>
              <p className="text-gray-700 text-base md:text-lg italic leading-relaxed">
                {member.description}
              </p>
            </div>
          </div>
        ))}
      </div>
    </section>
  );
}

